import React from "react";
import { classNames } from "../../utils/ClassNames";
import { useGetCharacterQuery } from "../../services/Characters/characters.api";
import { useParams } from "react-router-dom";
const CharPage = (props) => {
    const { id } = useParams();
    const { data, isLoading, error } = useGetCharacterQuery(Number(id));
    return (React.createElement("main", { className: classNames("char-page") }, isLoading ? null : JSON.stringify(data, null, 2)));
};
export default CharPage;
